import { useEffect } from "react";
import Crumbs from "components/Crumbs";
import Footer from "components/Footer";
import Header from "components/Header";
import Title from "components/Title";
import Typography from "components/Typography";
import AccountMenu from "components/AccountMenu";

import { useAuth } from "contexts/AuthContext";
import { useUsersPermissionsUsersRolesServiceGetUsersMe } from "openapi/swagger/queries";

import "./styles.scss";

const AccountPage = () => {
  const title = "Личный кабинет";
  const { data } = useUsersPermissionsUsersRolesServiceGetUsersMe();

  const { user, setUser } = useAuth();

  useEffect(() => {
    if (data) {
      setUser(data);
    }
  }, [data, setUser]);

  return (
    <>
      <div className="wrapper">
        <Title>{title}</Title>

        <Header />

        <div className="account">
          <Crumbs path={title || ""} />
          <Typography variant="h3">{user?.username}</Typography>
          <div className="account__header">
            <Typography className="account__header__item">
              {user?.name}
            </Typography>
            <Typography className="account__header__item">
              {user?.email}
            </Typography>
            <Typography className="account__header__item">
              {user?.phone}
            </Typography>
            {user?.type === "cosmetologist" && (
              <div className="account__header__label">Косметолог</div>
            )}
            {user?.type === "clinic" && (
              <div className="account__header__label">Клиника</div>
            )}{" "}
            {user?.type === "partner" && (
              <div className="account__header__label">Партнер</div>
            )}{" "}
            {user?.type === "brand" && (
              <div className="account__header__label">Бренд</div>
            )}
          </div>
          <AccountMenu />
        </div>
      </div>

      <Footer />
    </>
  );
};

export default AccountPage;
