import { useBrandServiceGetBrandsId } from "openapi/swagger/queries";
import { useMemo, useState } from "react";
import { useLocation, useNavigate, useParams } from "react-router-dom";

const useCatalogBrandPage = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { id }: any = useParams();
  const [show, setShow] = useState<"catalog" | "events">("catalog");
  const [sortingIsOpen, setSortingIsOpen] = useState(false);

  const { data, isLoading } = useBrandServiceGetBrandsId({
    id,
    populate: { logo: true },
  } as any);

  const brand = data?.data;

  const title = useMemo(() => {
    return brand?.name || "";
  }, [brand?.name]);

  const onChangeShow = (type: "catalog" | "events") => {
    setShow(type);
  };

  return {
    navigate,
    title,
    sortingIsOpen,
    setSortingIsOpen,
    show,
    onChangeShow,
    location,
    brand,
    isLoading,
  };
};

export default useCatalogBrandPage;
