import React, { createContext, useState, useEffect, ReactNode } from "react";
import { OpenAPI as OPENAPI_POSTMAN } from "openapi/postman/requests";
import {
  OpenAPI as OPENAPI_SWAGGER,
  Users_Permissions_User,
} from "openapi/swagger/requests";

interface AuthContextProps {
  id: number | undefined;
  token: string | undefined;
  isAuthenticated: boolean;
  login: (id: number, token: string) => void;
  logout: () => void;
  setToken: (id: number, token: string) => void;
  clearToken: () => void;
  user:
    | (Users_Permissions_User & {
        name?: string;
        phone?: string;
        type?: "cosmetologist" | "clinic" | "partner" | "brand";
      })
    | null;
  setUser: (user: Users_Permissions_User) => void;
}

const AuthContext = createContext<AuthContextProps | undefined>(undefined);

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [id, setId] = useState<number | undefined>(undefined);
  const [token, setStateToken] = useState<string | undefined>(undefined);
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(false);
  const [user, setUser] = useState<Users_Permissions_User | null>(null);

  useEffect(() => {
    const loadToken = () => {
      try {
        const localId = localStorage.getItem("id");
        const localToken = localStorage.getItem("token");
        if (localId && localToken) {
          setStateToken(localToken);
          setId(+localId);
          setIsAuthenticated(true);
        }
      } catch (error) {
        console.error("Не удалось загрузить токен", error);
      }
    };

    loadToken();
  }, []);

  const setToken = (id: number, newToken: string) => {
    setStateToken(newToken);
    setTokenInInterceptors(newToken);

    localStorage.setItem("token", newToken);
    localStorage.setItem("id", id + "");
  };

  const clearToken = () => {
    setStateToken(undefined);
    localStorage.removeItem("token");
    localStorage.removeItem("id");
    setTokenInInterceptors(undefined);
  };

  const login = (id: number, newToken: string) => {
    setToken(id, newToken);
    setIsAuthenticated(true);
  };

  const logout = () => {
    clearToken();
    setIsAuthenticated(false);
    // queryClient.resetQueries();
  };

  useEffect(() => {
    setTokenInInterceptors(token);
  }, [token]);

  const setTokenInInterceptors = (token: string | undefined) => {
    OPENAPI_POSTMAN.TOKEN = token;
    OPENAPI_SWAGGER.TOKEN = token;
  };

  return (
    <AuthContext.Provider
      value={{
        token,
        isAuthenticated,
        login,
        logout,
        setToken,
        clearToken,
        id,
        user,
        setUser,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = React.useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth должен быть использован внутри AuthProvider");
  }
  return context;
};
