import { Link } from "react-router-dom";

import Title from "components/Title";
import Header from "components/Header";
import Typography from "components/Typography";
import Loader from "components/Loader";

import { paths } from "constants/paths";
import { API_URL } from "constants/env";
import useCatalogBrandsPage from "./useCatalogBrandsPage";

import "./styles.scss";

const CatalogBrandsPage = () => {
  const { title, search, brands, onChangeSearch, isLoading } =
    useCatalogBrandsPage();

  return (
    <div className="wrapper">
      <Title>{title}</Title>

      <Header
        goBack={paths.home}
        title={"Каталог"}
        search={search}
        onChangeSearch={onChangeSearch}
      />

      <div className="catalog-brands">
        <Typography variant="h3" style={{ marginBottom: 8 }}>
          {title}
        </Typography>
        {isLoading ? (
          <Loader />
        ) : (
          <div className="catalog-brands__cards">
            {brands.map((el: any) => (
              <Link
                className="catalog-brands__card"
                key={el.id}
                to={`${paths.catalogBrands}/${el.documentId}`}
              >
                <img
                  src={`${API_URL}${el?.logo?.url}`}
                  alt={el.logo?.mime}
                  className="catalog-brands__card__image"
                />
                <Typography className="catalog-brands__card__title">
                  {el.name}
                </Typography>
              </Link>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default CatalogBrandsPage;
